import React, {Fragment, useState} from 'react';
import {connect} from 'react-redux';
import {getListTweets} from '../../store/tweet/tweetActions';
import AppHeader from '../header/Header';
import {MainWrapper} from '../../styles/globals';
import MainPageNav from '../pageNav/MainPageNav'
import AccountHeader from "../accHeader/AccountHeader";
import AccountMain from "../accMain/AccountMain";


const Home = () => {
    const [turnExp, setTurnExp] = useState(true);
    const [howIt, setHowIt] = useState(false);
    const [accBag, setAccBag] = useState(false);


    // componentDidMount() {
    //     this.props.getListTweets();
    // };

    const showTurnExp = () => {
        console.log('showTurnExp')
        setTurnExp(true)
        setHowIt(false)
        setAccBag(false)
    }
    const showHowIt = () => {
        console.log('showHowIt')
        setTurnExp(false)
        setHowIt(true)
        setAccBag(false)
    }
    const showAccBag = () => {
        console.log('showAccBag')
        setTurnExp(false)
        setHowIt(false)
        setAccBag(true)
    }

    return (
        <Fragment>
            <AppHeader/>
            <MainWrapper>
                <MainPageNav/>
                <AccountHeader
                    isShowTurnExp={() => showTurnExp()}
                    isShowHowIt={() => showHowIt()}
                    isShowAccBag={() => showAccBag()}
                />
                <AccountMain
                    turnExp={turnExp}
                    howIt={howIt}
                    accBag={accBag}
                    isShowTurnExp={() => showTurnExp()}
                    isShowHowIt={() => showHowIt()}
                    isShowAccBag={() => showAccBag()}
                />

            </MainWrapper>
        </Fragment>
    );
}

export default connect(null, {getListTweets})(Home);